                                                            {!isTokenOrderFetching ? !isUndefined(buyOrders) ? buyOrders.slice(0, 5).map((_order, index) => (
                                                                <tr key={`exchange-order-list-${index}`} onClick={() => { handleOrderClick(_order.price, _order.remain_amount, EXCHANGE_TYPE.SELL) }}>
                                                                    <td className="text-success">
                                                                        {formatDegits(_order.price, orderPrecision)}
                                                                    </td>
                                                                    <td className="text-center">{formatDegits(_order.remain_amount, 2)}</td>
                                                                    <td className="text-center">{formatDegits(_order.remain_amount * _order.price, 2)}</td>
                                                                </tr>)) : <tr><td> - </td><td> - </td><td> - </td></tr> : <tr>
                                                                <td className="text-success" colSpan={3}>
                                                                    <div className="d-flex justify-content-center"><CircularProgress /></div>
                                                                </td>
                                                            </tr>}
