import React, { useContext, useState } from "react";
import "react-perfect-scrollbar/dist/css/styles.css";
import "react-rangeslider/lib/index.css";
import { AuthContext } from "../../contexts/AuthContext";
import Footer2 from "../layout/footer2";
import Header2 from "../layout/header2";
import Sidebar from "../layout/sidebar";
import Axios from "axios"
import { BACKEND_API_URL } from "../../shared/constants";
import { useQuery } from "react-query";
import { useHistory } from "react-router-dom";
import { escape } from "../../shared/helpers";
import { LinearProgress } from "@mui/material";

function OnRamp() {

    const { currentUser, isAuthenticated, isAuthProcessing } = useContext(AuthContext)

    const [iframeLoading, setIFrameLoading] = useState(true)

    const history = useHistory()

    const fetchData = async () => {

        if (!isAuthenticated || !("email" in currentUser) || isAuthProcessing) {
            history.push('/signin')
            return false
        }

        try {
            const response = await Axios.post(`${BACKEND_API_URL}/auth/verified-all/${escape(currentUser?.id)}`, {})

            //TO-DO hash the content of return value

            const { user, verified } = response?.data

            if (!verified) {
                // check if email, phone verfied

                if (!user?.email_verified_at) {
                    history.push(`/verify-email/${user._id}`)
                } else if (!user?.phone_verified_at) {
                    history.push(`/otp-1`)
                } else if (!user?.id_verified_at) {
                    history.push(`/verify-step-1`)
                }
            }
        }

        catch (e) {
            console.log(e)
            history.push('/account-overview')
        }

        return false
    }

    const { isLoading, isError, data, refetch, error, isSuccess } = useQuery('email-verify', fetchData, {
        refetchOnWindowFocus: false,
        //enabled: false, // disable this query from automaticaslly running
        staleTime: Infinity,
        refetchInterval: 1000,
        retry: true
    })

    return (
        <>
            <Header2 />
            {isLoading && <LinearProgress color='info' />}
            <Sidebar activePage={`onRamp`} />
            <div className="content-body h-100" id="on_ramp">
                <div className="text-center" height="600px">
                    {/* {iframeLoading && <LinearProgress color='info' />}
                    <iframe
                        src="https://buy-sandbox.moonpay.com/"
                        width="400px"
                        height="600px"
                        onLoad={() => { setIFrameLoading(false) }}
                        className="pt-3"
                    ></iframe> */}
                    <h2 className="text-center">Coming Soon...</h2>
                </div>
            </div>
            <Footer2 />
        </>
    );
}

export default OnRamp;
