import React, { Component } from 'react';
import { Modal, Button, Card, OverlayTrigger, Tooltip, Accordion } from 'react-bootstrap';
import { Link } from 'react-router-dom';

export function StakeDialog({ onHide, show }) {
    return (
        <Modal
            onHide={onHide}
            show={show}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    Farm Dialog
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Card>
                    <Card.Header className='justify-content-center'>
                        <div className='row col-sm-12'>
                            <div className='d-flex flex-sm-column justify-content-between flex-md-row px-0'>
                                <Button variant={`primary`} className={`my-sm-2 my-md-1 px-5 py-2`}>30 Days<i className='fa fa-check'></i></Button>
                                <Button variant={`outline-primary`} className={`my-sm-2 my-md-1 px-5 py-2`}>60 Days</Button>
                                <Button variant={`outline-primary`} className={`my-sm-2 my-md-1 px-5 py-2`}>1 Year</Button>
                            </div>
                        </div>
                    </Card.Header>
                    <Card.Body>
                        <div className='row mb-3 align-items-center'>
                            <div className='col-sm-12'>
                                <div className="input-group mb-3">
                                    <div className="input-group-prepend">
                                        <label className="input-group-text text-white  bg-primary">Amount</label>
                                    </div>
                                    <input type="text" className="form-control text-end"
                                        placeholder="30 USDT" />
                                    <div className="input-group-prepend">
                                        <label className="input-group-text text-white  bg-secondary">MAX</label>
                                    </div>
                                </div>
                            </div>
                            <div className='col-md-12 mb-3'>
                                <p className={`mb-1 text-primary`}>Available: 78950.35 CAX
                                    <OverlayTrigger placement="right" overlay={<Tooltip id="available-info">Available token amount in your account to stake.</Tooltip>}>
                                        <span className='mx-1 text-info'><i className="fa fa-info-circle"></i></span>
                                    </OverlayTrigger>
                                </p>
                            </div>
                            <div className='col-md-12 mb-3 border-bottom'>
                                <h4 className='text-white'>
                                    Limit Amounts
                                    <OverlayTrigger placement="right" overlay={<Tooltip id="limit-amount-info">Maximum or minimum token amount you can stake.</Tooltip>}>
                                        <span className='mx-1 text-info'><i className="fa fa-info-circle"></i></span>
                                    </OverlayTrigger>
                                </h4>
                                <div className='col-md-12 d-flex justify-content-between'>
                                    <h4 className='text-danger'>Min:120CAX</h4>
                                    <h4 className='text-success'>Max:12000CAX</h4>
                                </div>
                            </div>
                            <div className='col-md-12 mb-3 border-bottom'>
                                <Accordion id="accordion-faq" defaultActiveKey='0' className="accordion">
                                    <Card>
                                        <Accordion.Toggle as={Card.Header} eventKey="0">
                                            <h4>Farm Period
                                                <OverlayTrigger placement="right" overlay={<Tooltip id="period-amount-info">Calculated farming start date and end date.</Tooltip>}>
                                                    <span className='mx-1 text-info'><i className="fa fa-info-circle"></i></span>
                                                </OverlayTrigger>
                                            </h4>
                                        </Accordion.Toggle>
                                        <Accordion.Collapse eventKey="0">
                                            <Card.Body>
                                                <div className='row mx-5'>
                                                    <div className='col-sm-2'>
                                                        <div className='d-flex justify-content-center mb-5'>
                                                            <span><i className='fa fa-circle text-primary'></i></span>
                                                            <div className='position-absolute stake-line'>
                                                                <i className='fa fa-arrow-down text-white'></i>
                                                            </div>
                                                        </div>
                                                        <div className='d-flex justify-content-center'>
                                                            <span><i className='fa fa-circle text-danger'></i></span>
                                                        </div>
                                                    </div>
                                                    <div className='col-sm-10'>
                                                        <div className='d-flex justify-content-between mb-5'>
                                                            <span>Start Date</span>
                                                            <span className='text-white'>12/03/2023, 00:59:09</span>
                                                        </div>
                                                        <div className='d-flex justify-content-between'>
                                                            <span>End Date</span>
                                                            <span className='text-white'>10/03/2024, 22:41:55</span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </Card.Body>
                                        </Accordion.Collapse>
                                    </Card>
                                </Accordion>
                            </div>
                            <div className='col-md-12 mb-3'>
                                <div className='d-flex justify-content-between'>
                                    <span>EST.APY</span>
                                    <span className='text-white'>10%</span>
                                </div>
                                <div className='d-flex justify-content-between'>
                                    <span>Estimated Interests</span>
                                    <span className='text-success'>0 CAX</span>
                                </div>
                            </div>
                            <div className='col-sm-12'>
                                <div class="form-check">
                                    <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
                                        <label class="form-check-label" for="flexCheckDefault">
                                        I have read and agree to the  <Link to={`/stake`}>Caladex Staking Service Agreement.</Link>
                                        </label>
                                </div>
                            </div>
                        </div>
                    </Card.Body>
                </Card>
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={onHide} className={`btn-danger`}>Close</Button>
                <Button onClick={onHide}>Confirm</Button>
            </Modal.Footer>
        </Modal >
    );
}
