import React, { useEffect, useRef } from 'react';
import PageTitle from '../element/page-title';
import AccountSubmenu from '../layout/account-submenu';
import Footer2 from '../layout/footer2';
import Header2 from '../layout/header2';
import Sidebar from '../layout/sidebar';
import { useContext } from 'react';
import { AuthContext } from '../../contexts/AuthContext';
import { useHistory, useParams } from 'react-router-dom';
import { useRecoilValue } from 'recoil';
import { atomTargetTokenList, atomTokenPairList, atomWalletTokenBalance } from '../../state/atoms';
import { isEmpty, isUndefined } from 'lodash';
import { BACKEND_API_URL, BACKEND_FILE_URL, CALADEX_ADDRESSES, CALADEX_ETH_ADDR, CHAIN_LIST, DEFAULT_TOKEN_SYMBOL } from '../../shared/constants';
import { CircularProgress } from '@mui/material';
import { useState } from 'react';
import { useWeb3React } from '@web3-react/core';
import { useAccountInfo, useWalletTokenBalance } from '../../state/hooks';
import toast from "react-hot-toast"
import Web3 from 'web3';
import { approveToken, depositToken, getGasPrice } from '../../shared/web3';
import { formatDegits } from '../../shared/helpers';
import Axios from "axios"



function AccountDeposit() {

    const { id } = useParams()

    const history = useHistory()

    const { currentUser, authToken, tokenPairRefresh, refreshTokenRefetch } = useContext(AuthContext)

    const { activate, active, account, chainId } = useWeb3React()

    const currentChainId = Web3.utils.numberToHex(chainId)

    const caladexAddress = CALADEX_ADDRESSES[currentChainId] === undefined ? CALADEX_ETH_ADDR : CALADEX_ADDRESSES[currentChainId]

    const tokenPairList = useRecoilValue(atomTokenPairList)

    const targetTokenList = useRecoilValue(atomTargetTokenList)

    const walletBalances = useRecoilValue(atomWalletTokenBalance)

    const { isRefetching: isWalletBalanceRefetching, refetch: walletBalanceRefetch } = useWalletTokenBalance()

    const { data, refetch: userInfoRefetch } = useAccountInfo(authToken, currentUser?.id)

    const [isDepositing, setIsdepositing] = useState(false)

    const [isInitialized, setInitialized] = useState(false)

    const [depositAmount, setDepositAmount] = useState(0)

    const [gasPrice, setGasPrice] = useState(0)

    const depositInputRef = useRef()

    useEffect(() => {
        tokenPairRefresh()
            .then(() => {
                setInitialized(true)
            })
    }, [])

    useEffect(() => {

        const fetchGasPrice = async () => {
            const _gasPrice = await getGasPrice()
            setGasPrice(_gasPrice)
        }
        if (active)
            fetchGasPrice()
        if (isInitialized)
            walletBalanceRefetch()

    }, [active, account, isInitialized])

    useEffect(() => {
        const fetchGasPrice = async () => {
            const _gasPrice = await getGasPrice(chainId)
            setGasPrice(_gasPrice)
        }

        fetchGasPrice()
    }, [chainId])

    const handleDeposit = async () => {

        const token = getTokenInfo()

        if (!active || Web3.utils.toHex(chainId) != token?.chain_id) {
            toast.error("Please connect your wallet or switch to network.")
            return false
        }

        if (isEmpty(currentUser) || !authToken) {
            toast.error("Please sign in to depsit token.")
            return false
        }

        if (!+depositAmount || !+getTokenBalance() || depositAmount > +getTokenBalance()) {
            toast.error("Please check token balance or input correct token amount.")
            depositInputRef.current.focus()
            return false
        }

        const _toast = toast.loading("Depositing token...")
        setIsdepositing(true)

        try {

            // send approve request
            const approveResult = await approveToken(account, token?.address, caladexAddress, +depositAmount, token?.decimal)

            await refreshTokenRefetch()

            // send token transfer request
            const depositResult = await depositToken(account, token?.address, caladexAddress, +depositAmount, token?.decimal)

            const { Deposited } = depositResult?.events

            const { blockNumber, transactionHash, signature } = Deposited

            // send server request            

            const response = await Axios.post(`${BACKEND_API_URL}/balance/deposit`, { blockNumber, transactionHash, signature, chainId: token?.chain_id }, { headers: { Authorization: `Bearer ${authToken}` } })

            toast.dismiss(_toast)
            toast.success("Token Successfully Deposited!")
            userInfoRefetch()

            setTimeout(() => { history.push('/account-overview') }, 1000)
        }
        catch (e) {

            console.log('deposit-error:', e)
            toast.dismiss(_toast)
            toast.error("Unexpected Error Occured!")

        }

        setIsdepositing(false)
        walletBalanceRefetch()
        return false
    }

    const getTokenInfo = () => {

        let token = {}
        if (isUndefined(id)) {
            token = [...tokenPairList, ...targetTokenList].find(_token => _token.chain_id == CHAIN_LIST[0]?.chainId && _token.symbol == DEFAULT_TOKEN_SYMBOL)
        } else {
            token = [...tokenPairList, ...targetTokenList].find(_token => _token?.token_id == id || _token?._id == id)
        }

        return token
    }

    const getTokenBalance = () => {
        const token = getTokenInfo()
        const balance = walletBalances.find(balance => balance.token_id == token?._id || balance.token_id == token?.token_id)

        return balance?.balance ?? 0
    }

    return (
        <>
            <Header2 />
            <Sidebar activePage={`account-overview`} />
            <PageTitle />

            <div className="content-body">
                <div className="container">
                    <div className="row">
                        <div className="col-xl-12">
                            <div className="card sub-menu">
                                <div className="card-body">
                                    <AccountSubmenu active={`deposit`} />
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-12">
                            <div className="card">
                                <div className="card-header">
                                    <h4 className="card-title">Wallet Deposit </h4>
                                    <h4 className='text-white'>{!isEmpty(tokenPairList) && !isEmpty(targetTokenList) ? getTokenInfo()?.symbol : ` - `} ({!isEmpty(tokenPairList) ? CHAIN_LIST.find(chain => chain.chainId == getTokenInfo()?.chain_id)?.chainName : ` - `})</h4>
                                </div>
                                <div className="card-body col-xl-10 mx-auto my-5" id="deposits">
                                    <div className="qrcode mx-auto">
                                        {!isEmpty(tokenPairList) && !isEmpty(targetTokenList) ? <img src={`${BACKEND_FILE_URL}/${getTokenInfo()?.logo_url}`} alt="" width="150" className='d-block mx-auto' /> : <div className='d-flex justify-content-center'><CircularProgress /></div>}
                                    </div>
                                    <form action='javascript:void(0)' className="py-5  deposit-form">

                                        <div className="mb-3 row align-items-center">
                                            <div className="col-sm-4">
                                                <label for="inputEmail3" className="col-form-label">Deposit Amount
                                                    <br />
                                                    <small className={!!+getTokenBalance() ? `text-primary` : `text-danger`}>Available Amount: {!isEmpty(walletBalances) && !isWalletBalanceRefetching ? getTokenBalance() : <CircularProgress sx={{ width: "0.8rem !important", height: "0.8rem !important" }} />} {!isEmpty(tokenPairList) && !isEmpty(targetTokenList) ? getTokenInfo()?.symbol : ` - `}</small>
                                                </label>
                                            </div>
                                            <div className="col-sm-8">
                                                <div className="input-group mb-3">
                                                    <div className="input-group-prepend">
                                                        <label className="input-group-text  bg-primary"><i
                                                            className="mdi mdi-currency-usd fs-18 text-white"></i></label>
                                                    </div>
                                                    <input type="text" className={`form-control text-end ${depositAmount > +getTokenBalance() ? `is-invalid` : ``}`}
                                                        placeholder="" value={depositAmount} onChange={(e) => { setDepositAmount(e.target.value) }} ref={depositInputRef} />
                                                    <div className="input-group-prepend">
                                                        <label className="input-group-text fs-18 bg-info text-white cursor-pointer" onClick={() => { setDepositAmount(getTokenBalance()) }}>Max</label>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="mb-3 row align-items-center">
                                            <div className="col-sm-6">
                                                <label for="inputEmail3" className="col-form-label">{!isEmpty(tokenPairList) && !isEmpty(targetTokenList) ? CHAIN_LIST.find(chain => chain.chainId == chainId)?.chainName : ` - `} Network Fee
                                                    ({!isEmpty(tokenPairList) && !isEmpty(targetTokenList) ? CHAIN_LIST.find(chain => chain.chainId == chainId)?.nativeCurrency?.symbol : ` - `})
                                                    <br />
                                                    <small>Transactions on the {!isEmpty(tokenPairList) && !isEmpty(targetTokenList) ? CHAIN_LIST.find(chain => chain.chainId == chainId)?.chainName : ` - `} network are priorirized by
                                                        fees</small>
                                                </label>
                                            </div>
                                            <div className="col-sm-6">
                                                <h4 className="text-end">{active && formatDegits(gasPrice, 2)}Gwei</h4>
                                            </div>
                                        </div>

                                        <div className="text-end">
                                            {!isDepositing ? < button className="btn btn-primary" disabled={!+getTokenBalance() || !depositAmount || depositAmount > +getTokenBalance()} onClick={() => { handleDeposit() }}>Deposit Now</button> : <CircularProgress />}
                                            {/* <button className="btn btn-primary" onClick={() => { handleDeposit() }}>Deposit Now</button> */}
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div >
            </div >
            <Footer2 />
        </>
    )
}

export default AccountDeposit;


