import React, { useContext, useState } from 'react';
import { Link } from 'react-router-dom';

import { CopyToClipboard } from 'react-copy-to-clipboard';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { useWeb3React } from '@web3-react/core';
import { walletConnect } from '../../shared/web3';
import { useEffect } from 'react';
import { AuthContext } from '../../contexts/AuthContext';
import { isEmpty } from 'lodash';

function PageTitle({ username }) {

    const { active, account, activate } = useWeb3React();

    const { currentUser, isAuthenticated } = useContext(AuthContext)

    const [copied, setCopied] = useState(false)

    const handleCopy = () => {
        setTimeout(() => {
            setCopied(true)
            setTimeout(() => { setCopied(false) }, 600)
        }, 100)
    }

    useEffect(() => {
        walletConnect(activate);
    }, [])

    return (
        <>
            <div className="page-title dashboard">
                <div className="container">
                    <div className="row">
                        <div className="col-8">
                            <div className="page-title-content">
                                <p>Welcome Back,
                                    <span> {username}</span>{`  `}
                                    {active ? <><span className='ml-3 text-success'>{active ? account : ` - `}</span>
                                        <OverlayTrigger placement="right" overlay={<Tooltip className='text-primary'>{copied ? `Copied` : `Copy`}</Tooltip>}>
                                            <CopyToClipboard text={account}>
                                                <span onClick={handleCopy}><i className="fa fa-copy text-danger" style={{ cursor: `pointer` }}></i></span>
                                            </CopyToClipboard>
                                        </OverlayTrigger></> : <span className='ml-3 text-danger' style={{ cursor: `pointer` }} onClick={() => { walletConnect(activate) }}>Please connect wallet to manage your balance.</span>}
                                </p>
                            </div>
                        </div>
                        <div className="col-4">
                            <ul className="text-end breadcrumbs list-unstyle">
                                {isAuthenticated && !isEmpty(currentUser) && <li className='active'><Link to={"./settings"}>Settings </Link></li>}
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default PageTitle;

