import React, { useEffect, useState } from 'react';
import { Button, Dropdown } from 'react-bootstrap';
import { Link, useHistory, useParams } from 'react-router-dom';
import { getTokenBalance, humanReadableAccount, walletConnect, walletDisconnect } from '../../shared/web3';
import { useWeb3React } from '@web3-react/core';
import { useContext } from 'react';
import { AuthContext } from '../../contexts/AuthContext';
import { isEmpty, isUndefined } from 'lodash';
import { useRecoilValue } from 'recoil';
import { atomTokenPairBalance, atomTokenPriceList, atomWalletCaladexBalance, atomWalletTokenBalance } from '../../state/atoms';
import { formatDegits, getSymbolsFromTicker, setRefreshToken } from '../../shared/helpers';
import { CircularProgress } from '@mui/material';
import { getTokenPrice } from '../../shared/ExchangeService';
import { CHAIN_LIST } from '../../shared/constants';
import { useWalletCaladexBalance, useWalletPairBalance, useWalletTokenBalance } from '../../state/hooks';

const ProfileToggle = React.forwardRef(({ children, onClick }, ref) => (
    <div
        ref={ref}
        onClick={e => {
            e.preventDefault();
            onClick(e);
        }}

    >
        {children}
        <div className="profile_log">
            <div className="user">
                <span className="thumb"><i className="mdi mdi-account"></i></span>
                <span className="arrow"><i className="la la-angle-down"></i></span>
            </div>
        </div>
    </div>
));




function Header2() {

    const { network, ticker } = useParams()


    const [targetTokenSymbol, pairTokenSymbol] = getSymbolsFromTicker(ticker)

    const history = useHistory()

    const { active, account, activate } = useWeb3React();

    const [availableBalance, setAvailableBalance] = useState(0)

    const { setAuthToken, currentUser, isAuthenticated, setAuthenticated, setCurrentUser, authToken, tokenPriceRefresh } = useContext(AuthContext)

    const [tokenBalance, pairBalance, usdBalance] = useRecoilValue(atomTokenPairBalance)

    const caladexBalances = useRecoilValue(atomWalletCaladexBalance)

    const walletBalances = useRecoilValue(atomWalletTokenBalance)
    const tokenPriceList = useRecoilValue(atomTokenPriceList)

    const { isRefetching: isCaladexBalanceRefetching } = useWalletCaladexBalance(authToken, currentUser?.id)
    const { isRefetching: isWalletBalanceRefetching, refetch: walletBalanceRefetch } = useWalletTokenBalance()

    useEffect(() => {

        walletBalanceRefetch()
            .then(() => {
                tokenPriceRefresh()
            })
    }, [active, account])


    const getTotalUsdBalance = (_balances) => {
        let totalBalance = 0
        let walletBalance = 0
        let caladexBalance = 0
        _balances.map(balance => {
            totalBalance += _getTokenUsdValue(balance).totalBalance
            walletBalance += _getTokenUsdValue(balance).walletBalance
            caladexBalance += _getTokenUsdValue(balance).caladexBalance
        })
        return { totalBalance, walletBalance, caladexBalance }
    }

    /**
     * 
     * @param {Object} _balance
     * @returns get total usd value for one token
     */
    const _getTokenUsdValue = (_balance) => {

        const _wallet_balance = walletBalances?.find(value => _balance.token_id._id === value.token_id)?.balance ?? 0
        const usdPrice = tokenPriceList.find(value => _balance.token_id.symbol == value.symbol)?.usdPrice ?? 0
        const totalBalance = +usdPrice * (+_balance.order_balance + +_balance.stake_balance + +_balance.caladex_balance + +_wallet_balance)
        const walletBalance = +usdPrice * +_wallet_balance
        const caladexBalance = +usdPrice * (+_balance.order_balance + +_balance.stake_balance + +_balance.caladex_balance)

        return { totalBalance, walletBalance, caladexBalance }
    }

    const handleLogout = (e) => {
        e.preventDefault()

        setAuthToken("")
        setRefreshToken("")
        setCurrentUser({})
        setAuthenticated(false)

        history.push("/signin")
        return false
    }

    const handleConnect = async (e) => {
        e.preventDefault()
        if (!active) {
            walletConnect(activate)
        } else {
            walletDisconnect(activate)
        }
    }

    return (
        <>
            <div className="header dashboard">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-xl-12">
                            <nav className="navbar navbar-expand-lg navbar-light px-0 justify-content-between">
                                <Link className="navbar-brand" to={"/"}><img src={require('./../../images/logo.png')} alt="" /></Link>

                                <div className="header-right d-flex my-2 align-items-center">
                                    <div className="signin-btn">
                                        {/* <Link to={'./dashboard'}>Dashboard</Link> */}
                                        <Button type="button" className="btn btn-primary mx-3 text-white" to={'#'} onClick={e => { handleConnect(e) }}>{active ? humanReadableAccount(account) : `Connect`}</Button>
                                    </div>
                                    <div className="dashboard_log">
                                        {isAuthenticated ? <Dropdown className="profile_log">
                                            <Dropdown.Toggle as={ProfileToggle} />
                                            <Dropdown.Menu size="sm" title="">
                                                <div className="user-email">
                                                    <div className="user">
                                                        <span className="thumb"><i className="mdi mdi-account"></i></span>
                                                        <div className="user-info">
                                                            <h6>{currentUser?.name}</h6>
                                                            <span>{currentUser?.email}</span>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div className="user-balance">
                                                    <div className="available">
                                                        <p>Available</p>
                                                        <span>{!isCaladexBalanceRefetching && !isEmpty(caladexBalances) ? formatDegits(getTotalUsdBalance(caladexBalances)?.caladexBalance, 2) : <CircularProgress sx={{ width: "1rem !important", height: "1rem !important" }} />} USD</span>
                                                    </div>
                                                    <div className="total">
                                                        <p>Total</p>
                                                        <span>{!isWalletBalanceRefetching && !isEmpty(walletBalances) && !isEmpty(caladexBalances) ? formatDegits(getTotalUsdBalance(caladexBalances)?.totalBalance, 2) : <CircularProgress sx={{ width: "1rem !important", height: "1rem !important" }} />} USD</span>
                                                    </div>
                                                </div>

                                                <Link to={"/account-overview"} className="dropdown-item">
                                                    <i className="mdi mdi-account"></i> Account
                                                </Link>
                                                <Link to={"/settings"} className="dropdown-item">
                                                    <i className="mdi mdi-settings"></i> Setting
                                                </Link>
                                                <Link to={"/lock"} className="dropdown-item">
                                                    <i className="mdi mdi-lock"></i> Lock
                                                </Link>
                                                <Link to={"#"} className="dropdown-item logout" onClick={e => { handleLogout(e) }}>
                                                    <i className="mdi mdi-logout"></i> Logout
                                                </Link>
                                            </Dropdown.Menu>
                                        </Dropdown> : <></>}
                                    </div>
                                </div>
                            </nav>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Header2;