import { atom, useRecoilState, useRecoilValue, useSetRecoilState } from "recoil"
import { atomTargetTokenList, atomTokenOrderList, atomTokenPairBalance, atomTokenPairList, atomTokenPriceList, atomTradingViewData, atomWalletCaladexBalance, atomWalletCaladexPairBalance, atomWalletTokenBalance } from "./atoms"
import { useQueryClient, useQuery } from "react-query"
import { BACKEND_API_URL, CHAIN_LIST, EXCHANGE_TYPE, TOKEN_POLLING_INTERVAL, WALLET_POLLING_INTERVAL } from "../shared/constants"
import Axios from "axios"
import Web3 from "web3"
import { getTokenBalance } from "../shared/web3"
import { getTokenPrice } from "../shared/ExchangeService"
import { useWeb3React } from "@web3-react/core"
import { isEmpty } from "lodash"
import { atomTokenTradeList } from "./atoms"
import moment from "moment"

/**
 * 
 * @returns get token pair list
 */

export const useTokenPairList = () => {

    const settokenPairList = useSetRecoilState(atomTokenPairList)

    const fetchData = async () => {

        const response = await Axios.post(`${BACKEND_API_URL}/tokeninfo/getall`, {})

        const { data } = response

        const compatibleData = data.data.map(token => {
            return {
                ...token.token_id,
                ...token,
                token_id: token.token_id._id,
                high24hr: token.day_high,
                low24hr: token.day_low,
                volume24hr: token.volume,
                percentChange: token.day_change,
                total24hr: 0,
                last: token.last_price
            }
        })

        settokenPairList(compatibleData)
    }

    return useQuery('token-pair-list', fetchData, {
        refetchOnWindowFocus: false,
        enabled: true,
        staleTime: Infinity,
        retryDelay: 4000,
        // refetchInterval: TOKEN_POLLING_INTERVAL,
        retry: true
    })
}

/**
 * 
 * @returns token balance for account
 */
export const useTargetTokenList = () => {

    const setTargetTokenList = useSetRecoilState(atomTargetTokenList)

    const fetchData = async () => {

        const response = await Axios.post(`${BACKEND_API_URL}/token/get/target/all`, {})

        const { data } = response

        setTargetTokenList(data.data)

        return false
    }

    return useQuery('target-token-list', fetchData, {
        refetchOnWindowFocus: false,
        enabled: true,
        staleTime: Infinity,
        retryDelay: 4000,
        // refetchInterval: TOKEN_POLLING_INTERVAL,
        retry: true
    })
}

/**
 * 
 * @returns all orders for token pair
 */

export const useTokenOrderList = (chain_id, token_symbol, pair_token) => {

    const tokenPairList = useRecoilValue(atomTokenPairList)
    const [tokenOrderList, setTokenOrderList] = useRecoilState(atomTokenOrderList)

    const fetchData = async () => {

        if (isEmpty(tokenPairList)) {
            return false
        }

        const token = tokenPairList.find(token => token.symbol === token_symbol?.toUpperCase() && token.chain_id === chain_id)

        const response = await Axios.post(`${BACKEND_API_URL}/order/get`, { chain_id, token_id: token?.token_id, pair_token: pair_token?.toUpperCase() })

        const { data } = response

        const buyOrders = data?.data?.data?.filter(_order => _order.type === EXCHANGE_TYPE.BUY && !_order.is_traded).sort((a, b) => +b.price - +a.price)
        const sellOrders = data?.data?.data?.filter(_order => _order.type === EXCHANGE_TYPE.SELL && !_order.is_traded).sort((a, b) => +b.price - +a.price)
        const allOrders = data?.data?.data?.sort((a, b) => moment(b.time).valueOf() - moment(a.time).valueOf())

        setTokenOrderList([getSamePriceMergedOrderList(buyOrders), getSamePriceMergedOrderList(sellOrders), allOrders])
    }

    return useQuery('token-order-list', fetchData, {
        refetchOnWindowFocus: false,
        enabled: true,
        staleTime: Infinity,
        retryDelay: 4000,
        refetchInterval: TOKEN_POLLING_INTERVAL,
        retry: true
    })

}

