export const useWalletTokenBalance = () => {

    const setWalletTokenBalance = useSetRecoilState(atomWalletTokenBalance)

    const tokenPairlist = useRecoilValue(atomTokenPairList)
    const targetTokenList = useRecoilValue(atomTargetTokenList)

    const { active, account, activate } = useWeb3React();

    const fetchData = async () => {

        if (!account || isEmpty(tokenPairlist) || isEmpty(targetTokenList)) {
            return false
        }

        let usdBalance = 0

        const walletBalances = await Promise.all([...tokenPairlist, ...targetTokenList].map(async (token, index) => {

            const chain = CHAIN_LIST.find(_chain => _chain.chainId === token.chain_id)
            const web3 = new Web3(chain?.rpcUrls[0])
            const _tokenBalance = await getTokenBalance(account, token.address, web3, token.decimal)

            const _usdPrice = await getTokenPrice(token.symbol)
            usdBalance += _tokenBalance * _usdPrice
            return { token_id: token.token_id ?? token._id, balance: _tokenBalance }
        }))

        setWalletTokenBalance(walletBalances)
        return false
    }

    return useQuery('wallet-token-balances', fetchData, {
        refetchOnWindowFocus: false,
        enabled: true,
        staleTime: Infinity,
        retryDelay: 4000,
        // refetchInterval: TOKEN_POLLING_INTERVAL,
        retry: true
    })

}

export const useTokenPriceList = () => {

    const setTokenPriceList = useSetRecoilState(atomTokenPriceList)

    const tokenPairlist = useRecoilValue(atomTokenPairList)
    const targetTokenList = useRecoilValue(atomTargetTokenList)

    const fetchData = async () => {

        if (isEmpty(tokenPairlist) || isEmpty(targetTokenList)) {
            return false
        }

        const targetPrices = await Promise.all(targetTokenList.map(async (token, index) => {

            const _usdPrice = await getTokenPrice(token.symbol)

            return { token_id: token._id, symbol: token.symbol, usdPrice: _usdPrice }
        }))

        const tokenPrices = tokenPairlist.map((token, index) => {

            const pairPrice = targetPrices.find(_price => _price.symbol == token.pair_token)?.usdPrice

            return { token_id: token.token_id, symbol: token.symbol, usdPrice: +token.price * +pairPrice }
        })

        setTokenPriceList([...tokenPrices, ...targetPrices])
        return false
    }

    return useQuery('token-price-lists', fetchData, {
        refetchOnWindowFocus: false,
        enabled: true,
        staleTime: Infinity,
        retryDelay: 4000,
        // refetchInterval: TOKEN_POLLING_INTERVAL,
        retry: true
    })

}

export const useWalletPairBalance = (_token, _pairToken, _chainId) => {

    const setTokenPairBalance = useSetRecoilState(atomTokenPairBalance)

    const tokenPairlist = useRecoilValue(atomTokenPairList)
    const targetTokenList = useRecoilValue(atomTargetTokenList)

    const { active, account, activate } = useWeb3React();

    const fetchData = async () => {

        if (!account || isEmpty(tokenPairlist) || isEmpty(targetTokenList)) {
            return false
        }

        let usdBalance = 0

        try {

            let chain = CHAIN_LIST.find(_chain => _chain.chainId === _chainId)
            const mainToken = tokenPairlist.find(token => token.symbol === _token?.toUpperCase() && token.chain_id === _chainId && token.pair_token === _pairToken?.toUpperCase())
            const web3 = new Web3(chain?.rpcUrls[0])
            const _tokenBalance = await getTokenBalance(account, mainToken.address, web3, mainToken.decimal)

            chain = CHAIN_LIST.find(_chain => _chain.chainId === _chainId)
            const pairToken = targetTokenList.find(token => token.symbol === _pairToken?.toUpperCase() && token.chain_id === _chainId)
            const pairWeb3 = new Web3(chain?.rpcUrls[0])
            const _pairBalance = await getTokenBalance(account, pairToken.address, pairWeb3, pairToken.decimal)

            let _tokenPrice = await getTokenPrice(_token)
            const _pairPrice = await getTokenPrice(_pairToken)

            _tokenPrice = !_tokenPrice ? mainToken.price * _pairPrice : _tokenPrice
            usdBalance = +_tokenBalance * _tokenPrice + +_pairBalance * _pairPrice


            setTokenPairBalance([_tokenBalance, _pairBalance, usdBalance])
        }
        catch (e) {
            throw new Error('wallet-pair-balance', e)
        }

        return false
    }

    return useQuery('wallet-pair-balances', fetchData, {
        refetchOnWindowFocus: false,
        enabled: true,
        staleTime: Infinity,
        retryDelay: 4000,
        // refetchInterval: WALLET_POLLING_INTERVAL,
        retry: true
    })

}


export const useWalletCaladexBalance = (authToken, user_id) => {

    const setWalletCaladexBalance = useSetRecoilState(atomWalletCaladexBalance)

    const fetchData = async ({ queryKey }) => {

        const [_, _authToken, _user_id] = queryKey

        if (!_authToken || !_user_id) {

            throw new Error('wallet-caladex-balance', "User not logged in or data is unavailable.")
        }

        try {

            const response = await Axios.post(`${BACKEND_API_URL}/balance/get/${_user_id}`, {}, { headers: { Authorization: `Bearer ${_authToken}` } })

            setWalletCaladexBalance(response.data.data.doc)
        }
        catch (e) {
            throw new Error('wallet-caladex-balance', e)
        }

        return false
    }

    return useQuery(['wallet-caladex-balances', authToken, user_id], fetchData, {
        refetchOnWindowFocus: false,
        enabled: true,
        staleTime: Infinity,
        retryDelay: 4000,
        // refetchInterval: TOKEN_POLLING_INTERVAL,
        retry: true
    })

}

