const getSamePriceMergedOrderList = (_orders) => {

    if (!_orders?.length) {
        return _orders
    }

    let resultArray = []
    let price = _orders[0]?.price
    let amount = 0

    for (let _order of _orders) {
        if (price === +_order.price) {
            amount += +_order.remain_amount
        } else {

            resultArray.push({ price, remain_amount: amount })

            price = +_order.price
            amount = +_order.remain_amount
        }
    }

    resultArray.push({ price, remain_amount: amount })

    return resultArray
}